/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type='search'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
  font-size: 100%; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
  -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
  border: 0; /* 1 */
  -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
  border: 0; /* 1 */
  padding: 0;
  white-space: normal; /* 2 */
  *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
  font-size: 100%; /* 1 */
  margin: 0; /* 2 */
  vertical-align: baseline; /* 3 */
  *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button; /* 2 */
  cursor: pointer; /* 3 */
  *overflow: visible; /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type='checkbox'],
input[type='radio'] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
  *height: 13px; /* 3 */
  *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type='search'] {
  -webkit-appearance: textfield; /* 1 */ /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
  overflow: auto; /* 1 */
  vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

body {
  font-family: 'Styrene A Web';
  min-width: 320px;
  max-width: 1440;
  background: white;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 110%;
  text-align: center;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  font-family: Styrene A Web;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 120%;
  color: #000000;
  margin-top: 70px;
  margin-bottom: 30px;
}

.filter {
  margin: auto;
  border-radius: 10px;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 130%;
  background-color: #f7f7f7;
  padding-top: 14px;
  padding-bottom: 14px;
  margin-bottom: 25px;
  border: none;
}

.program {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 1440px;
  padding-right: 77px;
  padding-left: 77px;
  align-items: flex-start;
  border: 3px solid #f2f2f2;
  padding-left: 77px;
  border-radius: 25px;
  margin-bottom: 20px;
}

.program__wrapper {
  display: flex;
}

.program__module1 {
  display: flex;
  margin-bottom: 72px;
}

.program__title {
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 120%;
  margin-bottom: 84px;
}

.program__module1--number {
  border-top: 2px solid #ff3535;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 120%;
  min-width: 155px;
  margin-right: 81px;
  padding-top: 30px;
}

.program__module1--list {
  display: flex;
  flex-direction: column;
  text-align: start;
  max-width: 360px;
  padding-right: 40px;
  padding-top: 30px;
}

li {
  list-style-type: disc;
  padding-bottom: 10px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
}

li::marker {
  color: #ff3535;
}

.footer {
  display: flex;
  max-width: 1440px;
  justify-content: space-between;
  margin: auto;
  margin-bottom: 171px;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 130%;
  min-height: 293px;
}

.footer__test {
  display: flex;
  flex-direction: column;
  background-color: #2d2c2a;
  color: white;
  text-align: left;
  padding-left: 60px;
  padding-right: 60px;
  padding-top: 52px;
  margin-left: 15px;
}

.footer__modules {
  display: flex;
  flex-direction: column;
  color: white;
  background-color: #ff3535;
  text-align: left;
  padding-left: 60px;
  padding-top: 52px;
  padding-right: 60px;
  margin-right: 15px;
}

.footer__modules--title,
.footer__test--title {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 130%;
  padding-bottom: 23px;
}

.program__img {
  border-radius: 50%;
  padding: 15px;
}

.program__like {
  align-self: end;
  padding-bottom: 40px;
  border: none;
  background: none;
}

.program__close {
  align-self: end;
  padding-top: 40px;
  color: #2d2c2a;
}

@media (max-width: 1280px) {
  .program__module1 {
    flex-direction: column;
  }

  .footer {
    flex-direction: column;
  }

  .footer__modules,
  .footer__test {
    width: auto;
    margin: 10px;
    padding-bottom: 40px;
  }
}

@media (max-width: 860px) {
  .program__wrapper {
    flex-direction: column;
  }

  .header {
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 120%;
    text-align: left;
    padding: 15px;
    margin: 0;
  }

  .program__title {
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 120%;
    text-align: left;
    padding: 15px;
    margin: 0px;
  }

  .program {
    padding: 0;
    margin: 0;
  }

  .program__module1 {
    margin-bottom: 0px;
  }

  .program__module1--number {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 130%;
    background-color: #f7f7f7;
    padding-top: 14px;
    padding-bottom: 14px;
    margin: 15px;
    border: none;
  }

  .program__module1--list {
    display: none;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 130%;
    margin: 25px;
    padding: 15px;
  }

  input:checked + ul {
    display: block;
  }

  .program__module1--number::before {
    content: '';
    width: 18px;
    height: 18px;
    background-image: url(/testalpha.github.io/static/media/icon.834d5c26.svg);
    position: absolute;
    left: 34px;
  }

  .footer__modules--title,
  .footer__test--title {
    font-weight: bold;
    font-size: 25px;
    line-height: 120%;
  }

  .footer__modules--text,
  .footer__test--list {
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 130%;
  }
  .footer {
    margin-bottom: 0px;
  }

  .program__wrapper {
    width: 100%;
  }
}


